.App {
  box-sizing: border-box;
  margin: 0;
}
button {
  border-radius: 50px;
  margin: 10px 0 0 24px;
}
.datetime {
  display: flex;
  flex-direction: column;
  margin-left: 1%;
  /* align-content: center; */
}
.datetime > h3 {
  margin: 0;
  padding: 0;
}
#current {
  display: flex;
  text-align: center;
  margin-right: 15%;
  border: solid;
  border-left: none;
  padding-right: 10px;
}
#otherFocast {
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  color: white;
  margin-left: 25px;
  margin-right: 25px;
}
.Dailyfocastlist {
  display: flex;
  flex-direction: column;
  margin-top: -5px;
  row-gap: 10px;
  overflow: auto;
  max-height: 200px;
  margin-bottom: 100px;
  width: 100%;
}
.Dailyfocastlist::-webkit-scrollbar-corner {
  display: none;
}
.HourlyFocastinglist {
  display: flex;
  /* flex-flow: row wrap; */
  text-align: center;
  overflow: auto;
  width: 100%;
  margin: auto;
  column-gap: 10px;
}
.HourlyFocastinglist::-webkit-scrollbar {
  display: none;
}
.Current-weather {
  display: flex;
  flex-direction: column;
  /* position: relative; */
  /* text-align: center; */
  color: rgb(253, 253, 253);
  margin-top: 50px;
  margin-left: 24px;
  justify-content: flex-end;
  max-width: 500px;
}
.Weather-imagecontainer {
  text-align: center;
}
.Weather-image {
  box-sizing: border-box;
  width: 427px;
  height: 427px;
  margin-top: 50px;
}
.App-header {
  background-image: url(./photos/weather1.png);
  width: "100%";
  /* height: "100vw"; */
  background-repeat: "no-repeat";
  background-size: "contain";
  background-position: "center";
}

.App-link {
  color: #61dafb;
}
.hourfocast {
  display: flex;
  flex-direction: column;
  border: 3px groove rgb(124, 123, 123);
  border-radius: 10px;
  padding: 0 3px 0 3px;
}
.dayfocast {
  display: flex;
  width: 100%;
  border: 3px groove rgb(124, 123, 123);
  border-radius: 10px;
  justify-content: space-evenly;
}
